import { Injectable } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';

import { BaseService } from 'src/app/demo/Services/baseService/base-service.service';
import { HttpService } from 'src/app/demo/Services/http/http.service';
import { ApiPath } from 'src/app/demo/_helpers/apiPath';
import { SubscribeAdd, SubscribeRe } from 'src/app/demo/_model/subscribe';
import { SubscribeType } from 'src/app/demo/_model/subscribeType';
import 'sweetalert2/src/sweetalert2.scss';
import Swal from 'sweetalert2/dist/sweetalert2.js';
@Injectable({
  providedIn: 'root'
})
export class MshpMembershipSharedService {
  AddForm: FormGroup;
  SubscribeTypes: SubscribeType[];
  Subscribes: SubscribeRe[];
  SubscribeTypeService: BaseService<SubscribeType>;
  SubscribeService: BaseService<SubscribeAdd>;
  constructor(private formBuilder: FormBuilder,  private httpSv: HttpService) {
    this.SubscribeTypeService = new BaseService<SubscribeType>(httpSv, ApiPath.SubscribeType);
    this.SubscribeService = new BaseService<SubscribeAdd>(httpSv, ApiPath.Subscribe);
   }
   initForm(data:SubscribeRe = null) {
    this.AddForm = this.formBuilder.group({
      id:[data?.id | 0],
      name: [data?.name, Validators.required],
      totalHours: [data?.totalHours, Validators.required],
      priceLE: [data?.priceLE, Validators.required],
      priceDollar: [data?.priceDollar, Validators.required],
      priceReyal: [data?.priceReyal, Validators.required],
      photoBase64: ['', Validators.required],
      subscribeTypeId: [data?.subscribeType?.id.toString(), Validators.required],
    });
}
Add(modalMember:any): void {
  let data: SubscribeAdd = this.AddForm.value;
  data.totalHours = data.totalHours.toString();

  this.SubscribeService.Add(data).subscribe((res) => {
    this.Subscribes.push(res);
    modalMember.hide();
      console.log(res);
  });
}

AddPhoto(inputValue: FileList) {
  let file = inputValue.item(0);
  let data: FormData = new FormData();

  data.append('file', file);

  let reader: FileReader = new FileReader();

  reader.onloadend = this._handleReaderLoaded.bind(this, );
  reader.readAsBinaryString(file);
}
_handleReaderLoaded(readerEvt) {
  var binaryString = readerEvt.target.result;

  let imageData = btoa(binaryString);

  console.log(btoa(binaryString));

  this.AddForm.controls["photoBase64"].setValue(imageData);
}
Edit(subscribe: SubscribeRe,modal:any){
this.initForm(subscribe);
modal.show();
}
editSubscribe(modaleditMember:any){
  let data: SubscribeAdd = this.AddForm.value;
  data.totalHours = data?.totalHours.toString();

  this.SubscribeService.Update(data).subscribe((res) => {
    modaleditMember.hide();
      console.log(res);
  });
}
confirmAlert(id :number) {
  Swal.fire({
      title: 'هل انت متاكد من حذف العضوية؟',
      text: 'اذا تم الحذف لن تكون قادرا علي الاسترجاع مرة اخرى',
      type: 'warning',
      showCloseButton: true,
      showCancelButton: true
  }).then((willDelete) => {
      if (willDelete.dismiss) {
          Swal.fire('', 'لم يتم الحذف', 'error');

      } else {
    this.SubscribeService.Delete(id).subscribe(res=>{
       let index = this.Subscribes.findIndex((x) => x.id == id);
        this.Subscribes.splice(index, 1);
          Swal.fire('', 'تم حذف العضوية', 'success');
    });
   }
  });
}

}
